import uniCalendar from '@/components/uni-calendar/uni-calendar.vue'
export default {
	components: {
		uniCalendar
	},
	data() {
		return {
			popupStatus: false,
			calendarStatus: false,
			status: true,
			statuss: false,
			Birthday: ''
		};
	},
	methods:{
		statusClick(value) {
			if (value == 1) {
				this.status = true
				this.statuss = false
			} else {
				this.status = false
				this.statuss = true
			}
		},
		openCalendar () {
			this.$refs.calendar.open();
			this.calendarStatus = true;
		},
		close() {
			this.calendarStatus = false
		},
		confirm(e) {
			this.Birthday = e.fulldate
		},
		submit() {
			this.popupStatus = true
		}
	}
}
